import React, { useState, useEffect } from 'react';
import stepsData from 'app/utils/stepsData';
import { FormBuilderWrapper, StepWrapper } from './styled';
import { Section, Question } from './form-sections';
import { useForm } from "react-hook-form";

const FormBuilder = () => {
  const { register, control, reset, setValue, getValues } = useForm();
  const [currentIndexForm, setCurrentIndexForm] = useState(0);

  useEffect(() => {
    const currentStep = stepsData[currentIndexForm];
    if (currentStep.should_save) {
      const values = getValues();
      localStorage.setItem('form-values', JSON.stringify(values));
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentIndexForm]);

  useEffect(() => {
    const formValues = localStorage.getItem('form-values');
    if (formValues) {
      reset(JSON.parse(formValues));
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const setNextStep = (value) => {
    console.log(currentIndexForm)
    const currentStep = stepsData[currentIndexForm];
    console.log(currentStep)
    if (currentStep.skipForQuestion) {
      const values = getValues(currentStep.skipForQuestion)
      const checkvalue =  values ? values.label : value.label
      console.log(checkvalue)
      console.log(currentStep.skipFor)
      if (checkvalue.includes(currentStep.skipFor)) {
        setCurrentIndexForm(currentStep.nextStep)
      } else {
        setCurrentIndexForm(currentIndexForm + 1)
      }
    }
    else
      setCurrentIndexForm(currentIndexForm + 1)
  }

  const generateFormStep = (step) => {
    switch (step.type) {
      case 'section':
        return (
          <Section
            hideNextButton={step.hide_next_button}
            content={step.content}
            onNextStep={(values) => setNextStep(values)}
          />
        )
      case 'question':
        return (
          <Question
            control={control}
            setValue={setValue}
            getValues={getValues}
            register={register}
            questionId={step.question_id}
            title={step.title}
            content={step.content}
            required={step.required}
            skipFor={step.skipFor}
            onPrevStep={() => setCurrentIndexForm(currentIndexForm - 1)}
            onNextStep={(values) => setNextStep(values)}
            itemtype={step.type}
          />
        )
      default:
        break;
    }
  }

  return (
    <FormBuilderWrapper>
      <StepWrapper>
        {generateFormStep(stepsData[currentIndexForm])}
      </StepWrapper>
    </FormBuilderWrapper>
  );
}

export default FormBuilder;
