import { Button } from 'app/form-builder/form-components/button/styled';
import React, { useEffect, useState } from 'react';
import { QuestionTitle } from './styled';
//import 'react-phone-number-input/style.css'
//import PhoneInput from 'react-phone-number-input'
import PhoneInput from 'react-phone-input-2'
import 'react-phone-input-2/lib/style.css'


const InputPhone = ({ placeholder, register, questionId, onNextStep, required, title, setValue, ismobileDevice }) => {
  const [phone, setPhone] = useState('');
  const [qtitle, setqtitle] = useState(title)

  useEffect(() => {
    setqtitle(title)
    setPhone('')
  }, [title]);

  const handleOk = () => {
    if (required) {
      if (phone) {
        setValue(questionId.toString(), phone.toString())
        if(phone.length < 10 ){
          console.log("inside less 10")
          const etitle = qtitle.replace("*", "Invalid Number *")
          setqtitle(etitle)
        }else 
          onNextStep()
      } else {
        console.log(qtitle.includes("required"))
        if (!qtitle.includes("required")) {
          const etitle = qtitle.replace("*", "required *")
          setqtitle(etitle)
        }
      }
    } else {
      onNextStep()
    }
  }

  return (
    <>
      <QuestionTitle style={{ color: qtitle.includes("required") ? "red" : "black" }}>{qtitle}</QuestionTitle>
      <PhoneInput
        country={"ca"}
        disableCountryGuess={false}
        {...register(questionId.toString())}
        placeholder={placeholder}
        value={phone}
        style={{marginLeft: ismobileDevice ? 18 : 0}}
        onChange={phone => setPhone(phone)}
        inputStyle={{ border: 'none', backgroundColor: "#fafafb", fontSize:26, }}
        inputProps={{
          required: true,
          autoFocus: true,
          height:40, 
        }}
      />
      <Button style={{ marginTop: 40, width: 155, height: 45, color: "white", backgroundColor: "#ff858a", marginLeft: ismobileDevice ? 18 : 0 }} onClick={handleOk}> OK </Button>

    </>
  );
}

export default InputPhone;