import React from 'react';
import { SectionWrapper, Text, SecondText } from './styled';
import { ButtonComp } from 'app/form-builder/form-components';

const Section = ({ content, onNextStep, hideNextButton }) => {
  return (
    <SectionWrapper>
      {content.map((item, index) => (
        item.type === "Text" ?  <Text key={index}>{item.value}</Text> : <SecondText key={index}>{item.value}</SecondText>
      ))}

      {!hideNextButton &&
        <ButtonComp onClick={onNextStep}>Enter</ButtonComp>
      }
    </SectionWrapper>
  );
}

export default Section;
