import React, { useState, useRef } from 'react';
import { RadioImagesWrapper, CheckboxOption } from './styled';
import S3 from "react-aws-s3";
import ScanbotSDK from 'scanbot-web-sdk/webpack'
import Drawer from './Drawer';
import { ScanBot } from './ScanBot';



const RadioAction = ({ values, questionId, setValue, getValues, onNextStep, ismobileDevice }) => {
  const [itemChecked, setItemChecked] = useState(getValues(questionId.toString()) || {});
  const [imageLocation, setImageLocation] = useState('')
  const [scanbotSDK, setscanbotSDK] = useState(undefined)
  const [cvisible, setcVisbile] = useState(false)
  const fileInput = useRef();
  console.log(ismobileDevice)
  const handleUploadChange = () => {
    let file = fileInput.current.files[0];
    let newFileName = fileInput.current.files[0].name.replace(/\..+$/, "");
    console.log(newFileName)
    onNextStep()
  }

  const handleChange = async (option) => {
    const scanbotSDK = await ScanbotSDK.initialize({
    });
    setscanbotSDK(scanbotSDK)
    setcVisbile(true)
    let valueSelected = getValues(questionId.toString());
    if (!valueSelected) {
      valueSelected = {};
    }
    if (valueSelected.id === option.id) {
      setItemChecked({});
      setValue(questionId.toString(), {});
    } else {
      setItemChecked(option);
      setValue(questionId.toString(), option);
    }
  };

  const closeDrawer = () => {
    setcVisbile(false)
  }

  const setImageLocationFn = (imagelocation) => {
    console.log(imagelocation)
    setImageLocation(imagelocation)
    onNextStep()
  }
  return (
    <>
      <RadioImagesWrapper>
        {values.map(val => (
          <CheckboxOption key={val.id}>
            {val.label.includes("Upload") ?
              <>
                <img src={val.image} alt="" />
                <span style={{ color: "black" }}>{val.label}</span>
                <input id='fileUpload' type='file' ref={fileInput} style={{ display: 'none' }} onChange={handleUploadChange} />
                <div></div>
              </>
              :
              <>
                <img src={val.image} alt="" />
                <span style={{ color: "black" }}>ScanbotSDK</span>
                <input
                  defaultChecked={itemChecked.id === val.id}
                  onClick={() => handleChange(val)}
                  type="radio"
                  name={questionId}
                  value={val.id}
                />
                <div></div>
              </>
            }
          </CheckboxOption>
        ))}
      </RadioImagesWrapper>
      <Drawer open={cvisible} position='right' onDismiss={() => setcVisbile(false)} backgroundColor='#131445' size={ismobileDevice ? window.innerWidth : "542px"}>
        {scanbotSDK ?
          <ScanBot closeModal={closeDrawer} setImageLocation={setImageLocationFn} scanbotSDK={scanbotSDK} />
          :
          undefined
        }
      </Drawer>
    </>
  );
}

export default RadioAction;
